.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
      transform: rotate(0deg);
  }
  to {
      transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: Tahoma, Helvetica, Arial, Batang, "Microsoft Yahei","\5FAE\8F6F\96C5\9ED1", STXihei, "\534E\6587\7EC6\9ED1", sans-serif;
}

